.ListOfTasks {
    padding-left: 16px;
    padding-right: 16px;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    overflow-y: overlay;
}

.ListOfTasks ul {
    width: 100%;
}

.ListOfTasks .task {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: .5px solid #ddd;
    margin-bottom: 4px;
    padding-bottom: 4px;
}

.remove-task, .complete-task {
    background: none;
    border: none;
}

.ListOfTasks .task span {
    width: 85%;
    font-size: 16px;
    text-align: left;
}

.ListOfTasks .task .buttons {
    width: 15%;
    height: 100%;
    display: flex;
    justify-content: space-evenly;
}